<template>
  <div class="wrapper">
    <div class="settings">
      <button class="btn autoscroll" v-if="autoscroll" @click="autoscroll = !autoscroll"> Auto Scroll: active</button>
      <button class="btn autoscroll" v-if="!autoscroll" @click="autoscroll = !autoscroll"> Auto Scroll: not active
      </button>
    </div>

    <div class="list" id="list" ref="list">
      <HistoryListItem v-for="event in getEvents" :key="event.timestamp" :event="event"
                       @setDetails="setDetails"></HistoryListItem>
    </div>
  </div>

</template>

<script>
import HistoryListItem from "@/components/HistoryListItem";
import {api_get_call} from "@/api_calls";

export default {
  name: "HistoryList",
  components: {HistoryListItem},
  data() {
    return {
      event_list: [],
      autoscroll: true,
      pullEventsInterval: '',
      autoScrollInterval: ''
    }
  },
  methods: {
    setDetails(event) {
      this.$emit('setDetails', event)
    },
    scrollToBottom() {
      let container = this.$refs.list
      container.scrollTop = container.scrollHeight
    }
  },
  computed: {
    getEvents() {
      return this.event_list
    }
  },
  async mounted() {
    let data = await api_get_call("http://localhost:3500/api", "/events/initial")
    console.log(data.result)

    for (let e in data.results) {
      this.event_list.push(data.results[e])
    }

    this.pullEventsInterval = setInterval(async () => {
      let events = await api_get_call("http://localhost:3500/api", "/events/latest")
      for (let e in events.results) {
        this.event_list.push(events.results[e])
      }
    }, 1000)


    this.autoScrollInterval = setInterval(() => {
      if (this.autoscroll) {
        this.scrollToBottom()
      }
    }, 100)
  },
  beforeUnmount() {
    clearInterval(this.autoScrollInterval)
    clearInterval(this.pullEventsInterval)
  }
}
</script>

<style scoped>

.autoscroll {
  width: 200px;
}

.settings {
  display: flex;
}

.wrapper {
  display: flex;
  flex-direction: column;
  row-gap: 10px;
  max-height: fit-content;
  overflow: auto;
}

.list {
  display: flex;
  flex-direction: column;
  overflow-y: auto;
}
</style>