<template>
  <div class="history">
    <HistoryList :events="getEvents" @setDetails="setDetails"></HistoryList>
    <EventDetails :event_data="getSelectedEvent"></EventDetails>

  </div>

</template>

<style scoped>
.history {
  display: grid;
  grid-template-columns: 4fr 2fr;
}



</style>

<script>
import EventDetails from "@/components/InformationPanel";
import HistoryList from "@/components/HistoryList";

export default {
  components: {EventDetails, HistoryList},
  data() {
    return {
      selectedEvent: {}
    }
  },
  computed: {
    getEvents() {
      return this.events
    },
    getSelectedEvent() {
      return this.selectedEvent
    }
  },
  methods: {
    setDetails(event) {
      this.selectedEvent = event
    }
  },

}
</script>

